<template>
  <!-- 商品信息 -->
  <div class="order-shopinfo">
    <div class="title">商品信息</div>

    <div class="table-content" v-if="shopinfo.list.length !== 0">
        <table class="table">
            <tr>
                <th style="width: 37%">名称</th>
                <th  style="width: 21%">单价</th>
                <th  style="width: 21%">数量</th>
                <th  style="width: 21%">价格</th>
            </tr>
            <tr v-for="(item, index) in shopinfo.list" :key="item.food_id">
                <td>{{ item.food_name }}</td>
                <td>&yen; {{ item.food_price}}</td>
                <td>{{ item.food_num }}</td>
                <td>&yen; {{ item.food_price_sum}}</td>
            </tr>
            <tr>
                <td  colspan="3">
                    商品总数: {{shopinfo.listNum}}
                </td>
                <td>
                    总计: &yen; {{shopinfo.listTotalPrice }}
                </td>
            </tr>

            <tr class="last">
                <td colspan="4" class="clearfix">
                    <div class="td-title left">备注:</div>
                    <div class="remark-content left">{{ shopinfo.remark }}</div>
                </td>
            </tr>
        </table>
    </div>    
  </div>
</template>

<script>
export default {
  props: {
    shopinfo: {
      type: Object
    }
  },
  data() {
    return {};
  }
};
</script>

<style lang="scss" scoped>
@import "src/styles/mixin.scss";

.title {
  @include fc(14px,#666);
  margin: 20px 0;
  font-weight: bold;
}
.table-content {
  //   border-top: 1px solid #d8d8d8;
  //   border-bottom
}
.table {
  @include fc(14px,#333);
  border-top: 1px solid #d8d8d8;
  border-left: 1px solid #d8d8d8;
  border-collapse: collapse;
  width: 100%;

  th {
    border-bottom: 1px solid #d8d8d8;
    font-weight: normal;
    height: 50px;
  }

  th:last-child {
    border-right: 1px solid #d8d8d8;
  }

  td {
    border-right: 1px solid #d8d8d8;
    border-bottom: 1px solid #d8d8d8;
    text-align: center;
    height: 50px;
  }
  tr.last {
    td {
      text-align: left;
      padding: 20px 40px;
      div {
        height: 100%;
        //   @include wh(100%, 100%);
      }
      div.td-title {
        padding-right: 5px;
      }
      div.remark-content {
        max-width: 90%;
      }
    }
  }
}
</style>




